
import React from "react";
import {render} from "react-dom";
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import {Tabs, Tab} from 'material-ui/Tabs';
import {Link} from 'react-router';
import SwipeableViews from 'react-swipeable-views';
const styles = {
    headline: {
        fontSize: 24,
        paddingTop: 16,
        marginBottom: 12,
        fontWeight: 400,
    },
    slide: {
        padding: 10,
    },
};
class ArtTabs extends React.Component {
    constructor(props) {
        super(props);
        this.displayName = "ArtTabs";
        this.state = {
            slideIndex: 0,
        };
        this.handleChange = (value) => {
            this.setState({
                slideIndex: value,
            });
        };
    }
    render() {
        return (
            <div>
                <MuiThemeProvider>
                    <Tabs value={this.state.slideIndex}>
                        <Tab label="全部" Link value={0}/>
                        <Tab label="精华" value={1} />
                        <Tab label="分享" value={2} />
                        <Tab label="问答" value={3} />
                        <Tab label="招聘" value={4} />
                    </Tabs>
                    {/*<SwipeableViews*/}
                    {/*index={this.state.slideIndex}*/}
                    {/*onChangeIndex={this.handleChange}*/}
                    {/*>*/}
                    {/*<div>*/}
                    {/*<h2 style={styles.headline}>Tabs with slide effect</h2>*/}
                    {/*Swipe to see the next slide.<br />*/}
                    {/*</div>*/}
                    {/*<div style={styles.slide}>*/}
                    {/*slide n°2*/}
                    {/*</div>*/}
                    {/*<div style={styles.slide}>*/}
                    {/*slide n°3*/}
                    {/*</div>*/}
                    {/*</SwipeableViews>*/}
                </MuiThemeProvider>
            </div>
        )
    }
}
export default  ArtTabs


